import React, { Fragment } from 'react';
import { ThemeProvider } from 'styled-components';
import Sticky from 'react-stickynode';
import { DrawerProvider } from '../context/drawerContext';
import { saasModernTheme } from '../theme/saasModern';
import { ResetCSS } from '../assets/styles/style';
import { GlobalStyle, ContentWrapper } from '../components/SaasModern/saasModern.style';

import Hero from '../components/theme/Banner/index';
import Navbar from '../components/theme/Navbar/index';
import BenefitsSection from '../components/theme/WorkingProcess/index';
import PricingSection from '../components/theme/Pricing/index';
import PartnerSection from '../components/theme/Partner/index';
import FaqSection from '../components/theme/Faq/index';
import FutureFeaturesSection from '../components/theme/Feature/index';
import FeaturesSection from '../components/theme/UpdateScreen/index';
import TestimonialSection from '../components/theme/Testimonial/index';
import Footer from '../components/theme/Footer/index';
import SEO from '../components/Seo/seo';

export default () => {
    return (
        <ThemeProvider theme={saasModernTheme}>
            <Fragment>
                <SEO title="Fantasy Franchise" />

                <ResetCSS />
                <GlobalStyle />

                <div className="page--index">
                    <ContentWrapper>
                        <Sticky top={0} innerZ={9999} activeClass="sticky-nav-active">
                            <DrawerProvider>
                                <Navbar />
                            </DrawerProvider>
                        </Sticky>
                        <Hero />
                        <BenefitsSection />
                        <FeaturesSection />
                        <FutureFeaturesSection />
                        <PricingSection />
                        <PartnerSection />
                        <TestimonialSection />
                        <FaqSection />
                        <Footer />
                    </ContentWrapper>
                </div>
            </Fragment>
        </ThemeProvider>
    );
};
