import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
// import Image from 'gatsby-image';
import PropTypes from 'prop-types';
import Icon from 'react-icons-kit';
// import Fade from 'react-reveal/Fade';
import Box from '../Box/index';
import Text from '../Text/index';
import Heading from '../Heading/index';
import Button from '../Button/index';
import Container from '../Container/index';
import TiltShape from '../TiltShape/index';
import { BannerWrapper, DiscountWrapper, DiscountLabel } from './banner.style';
import { ic_play_circle_filled } from 'react-icons-kit/md/ic_play_circle_filled';
// import RegisterForm from '../../RegisterForm';
import { RegisterAndCreateLeagueForm } from '../../LeagueForm/RegisterAndCreateLeagueForm';

const BannerSection = ({
    row,
    contentWrapper,
    discountAmount,
    discountText,
    title,
    description,
    imageWrapper,
    buttonWrapper,
    button,
    fillButton,
}) => {
    const Data = useStaticQuery(graphql`
        query {
            bannerImage: file(relativePath: { eq: "banner-image.png" }) {
                childImageSharp {
                    fluid(maxWidth: 1170, quality: 100) {
                        ...GatsbyImageSharpFluid_noBase64
                    }
                }
            }
        }
    `);

    return (
        <BannerWrapper id="banner_section">
            <TiltShape />
            <Container>
                <Box {...row}>
                    <Box {...contentWrapper}>
                        <DiscountWrapper>
                            <DiscountLabel>
                                <Text {...discountAmount} content="25% Save" />
                                <Text {...discountText} content="for first month trail version" />
                            </DiscountLabel>
                        </DiscountWrapper>
                        <Heading
                            {...title}
                            content="Ultimate Platform to monitor your best workflow."
                        />
                        <Text
                            {...description}
                            content="For Enhanced performance we use LiteSpeed Web Server, HTTP/2, PHP7. We make your website faster, which will help you to increase search ranking!"
                        />
                        <Box {...buttonWrapper}>
                            <a href="#1">
                                <Button {...fillButton} title="FREE TRIAL" />
                            </a>
                            <a href="#1">
                                <Button
                                    {...button}
                                    title="WATCH VIDEO"
                                    icon={<Icon icon={ic_play_circle_filled} size={30} />}
                                    iconPosition="left"
                                />
                            </a>
                        </Box>
                    </Box>
                    <Box {...imageWrapper}>
                        {/* <Fade bottom> */}
                        <div>
                            <div
                                style={{ maxWidth: '600px', margin: 'auto' }}
                                className="bg-white rounded-lg min-h-[500px] relative"
                            >
                                <RegisterAndCreateLeagueForm standalone />
                            </div>
                        </div>
                        {/* </Fade> */}
                    </Box>
                </Box>
            </Container>
        </BannerWrapper>
    );
};

BannerSection.propTypes = {
    row: PropTypes.object,
    contentWrapper: PropTypes.object,
    discountAmount: PropTypes.object,
    discountText: PropTypes.object,
    title: PropTypes.object,
    description: PropTypes.object,
    imageWrapper: PropTypes.object,
    buttonWrapper: PropTypes.object,
    button: PropTypes.object,
    fillButton: PropTypes.object,
};

BannerSection.defaultProps = {
    row: {
        flexBox: true,
        flexWrap: 'wrap',
        alignItems: 'center',
        justifyContent: 'center',
    },
    contentWrapper: {
        width: ['100%', '100%', '80%', '55%', '50%'],
        mb: '40px',
    },
    imageWrapper: {
        width: '100%',
    },
    title: {
        fontSize: ['24px', '32px', '40px', '42px', '46px'],
        fontWeight: '700',
        color: '#fff',
        letterSpacing: '-0.025em',
        mb: ['20px', '25px', '25px', '25px', '25px'],
        lineHeight: '1.2',
        textAlign: 'center',
    },
    description: {
        fontSize: ['15px', '16px', '16px', '16px', '16px'],
        color: '#fff',
        lineHeight: '1.75',
        mb: '0',
        textAlign: 'center',
    },
    discountAmount: {
        fontSize: ['13px', '14px', '14px', '14px', '14px'],
        fontWeight: '700',
        color: '#00865b',
        mb: 0,
        as: 'span',
        mr: '0.4em',
    },
    discountText: {
        fontSize: ['13px', '14px', '14px', '14px', '14px'],
        fontWeight: '700',
        color: '#fff',
        mb: 0,
        as: 'span',
    },
    fillButton: {
        type: 'button',
        fontSize: ['13px', '14px'],
        fontWeight: '600',
        borderRadius: '4px',
        p: ['0px 15px', '8px 22px'],
        colors: 'secondaryWithBg',
        minWidth: ['auto', '150px'],
        height: ['40px', '46px'],
        minHeight: 'auto',
    },
    buttonWrapper: {
        flexBox: true,
        justifyContent: 'center',
        mt: '35px',
    },
    button: {
        type: 'button',
        fontSize: ['13px', '14px'],
        fontWeight: '600',
        borderRadius: '4px',
        p: ['0px 15px', '8px 22px'],
        color: '#fff',
        colors: 'secondary',
        height: ['40px', '46px'],
        minHeight: 'auto',
    },
};

export default BannerSection;
